.contaioner {
  width: 100%;
  height: 100vh;
  position: relative;
  background: url('../images/background.jpg') repeat;

  .blogWrap {
    padding:20px 0;
    max-width: 1000px;
    margin: auto;
    > p {
      font-family: "Playwrite AU SA", serif;
      font-weight: 200;
      padding:30px 0 0 0;
      text-align: center;
      font-size:28px;
      font-weight: bold;
      color:#1c1c1c;
    }
  }
  .blogEdit  {
    width: 90%;
    margin:30px auto;
    text-align: center;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    textarea {
      width: 100%;
      height:100px;
      padding: 10px;
      resize: none;
      border:none;
      border-radius: 10px;
      background-color: #eee;
    }
    .inputWrap {
      display: flex;
      align-content: center;
      justify-content: space-between;
      input {
        padding: 10px;
        border:none;
        background: #eee;
        border-radius: 10px;
        width: 25%;
      }
      button .icoChk {
        font-size: 24px;
        color:#d6d6d6;
        &:hover {
          color: #24170B;
        }
      }
    }
  }
}

.blogList {
  width: 90%;
  max-width:1000px;
  margin:auto;
  padding: 20px;
  border-radius: 10px;
  background: #ffffff;
  .blogItem {
    background: #eeeeee;
    padding:10px 20px;
    margin:10px;
    border-radius: 10px;
    p {
      font-size:15px;
    }
    .date {
      display: block;
      color:#AAA;
      padding-bottom:10px;
      font-size: 13px;
    }
    .content {
      font-size: 17px;
    }
    .btnWrap {
      text-align: right;
    }
    button {
      cursor: pointer;
      padding: 2px 5px;
      margin-right:5px;
      font-size: 18px;
      color: #666;
      &:hover {
        color: #0e0c1b;
      }
    }
    textarea {
      width: 100%;
      height: 40px;
      padding:10px;
      resize: none;
      box-sizing: border-box;
    }
  }
}

/* Mobile */
@media only screen and (max-width: 600px) {
  .contaioner {
    .blogWrap {
      padding: 4vw 0;
      height: 100vh;
      > p {
        padding: 3vw 10vw;
        font-size: 5vw;
      }
    }
    .blogEdit  {
      margin:4vw auto;
      padding: 3vw;
      border-radius: 0;
      textarea {
        height:20vw;
        padding: 3vw;
        border-radius: 1vw;
        border: 0.3vw solid #c9c9c9;
      }
      .inputWrap {
        input {
          padding: 3vw;
          border-radius: 1vw;
          width: 70%;
          border: 0.3vw solid #c9c9c9;
        }
        button .icoChk {
          font-size: 7vw;
          margin-right: 1vw;
          :hover, :active {
            color: #e23535;
          }
        }
      }
    }
  }
  .blogList {
    padding: 2vw;
    border-radius: 0;
    .blogItem {
      padding:3vw 4vw;
      margin:4vw 3vw;
      border: 0.3vw solid #c9c9c9;
      border-radius: 1vw;
      p {
        font-size:3.5vw;
      }
      .date {
        padding-bottom:2vw;
        font-size: 2.8vw;
      }
      .content {
        font-size:3.8vw;
      }
      button {
        padding: 1vw 2vw;
        margin-right:1vw;
        font-size: 4vw;
      }
      textarea {
        height: 15vw;
        padding:2vw;
        font-size: 3.5vw;
      }
    }
  }
}